import { Injectable } from '@angular/core';
import { FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { DatePipe } from '@angular/common';

@Injectable({
  providedIn: 'root'
})
export class UserService {

  constructor( private datePipe: DatePipe ) { }

  form: FormGroup = new FormGroup({
    passport: new FormControl('', [Validators.required, Validators.minLength(4)]),
    first_name: new FormControl('', [Validators.required, Validators.minLength(4)]),
    middle_name: new FormControl('', [Validators.required, Validators.minLength(4)]),
    last_name: new FormControl('', [Validators.required, Validators.minLength(4)]),
    username: new FormControl('', [Validators.required, Validators.minLength(4)]),
    email: new FormControl('', [Validators.email, Validators.required, Validators.minLength(6)]),
    password: new FormControl('', [Validators.required, Validators.minLength(6)]),
    phone: new FormControl('', [Validators.required, Validators.minLength(10)]),
    gender: new FormControl('1'),
    investment_capital: new FormControl('', Validators.required),
    education: new FormControl('', [Validators.required, Validators.minLength(4)]),
    experiance: new FormControl('', [Validators.required, Validators.minLength(4)]),
    birth_date: new FormControl('', [Validators.required, Validators.minLength(4)]),
    nationality: new FormControl('', [Validators.required, Validators.minLength(4)]),
    city: new FormControl('', [Validators.required, Validators.minLength(4)]),
    subcity: new FormControl('', [Validators.required, Validators.minLength(4)]),
    woreda: new FormControl('', [Validators.required, Validators.minLength(2)]),
    kebele: new FormControl('', [Validators.required, Validators.minLength(2)]),
    house_number: new FormControl('', [Validators.required, Validators.minLength(3)]),
    post_box: new FormControl('', [Validators.required, Validators.minLength(3)])
  });

  initializeFormGroup() {
    this.form.setValue({
    passport: '',
    first_name: '',
    middle_name: '',
    last_name: '',
    username: '',
    email: '',
    password: '',
    phone: '',
    gender: '1',
    investment_capital: '0',
    education: '',
    experiance: '',
    birth_date: '',
    nationality: 'Ethiopia',
    city: '',
    subcity: '',
    woreda: '',
    kebele: '',
    house_number: '',
    post_box: ''
    });
  }

}
