import { Injectable } from '@angular/core';
import { FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { DatePipe } from '@angular/common';

@Injectable({
  providedIn: 'root'
})
export class FarmlandService {

  constructor( private datePipe: DatePipe ) { }

  form: FormGroup = new FormGroup({
    farmland_area : new FormControl('', Validators.required),
    farmland_experiance: new FormControl('', Validators.required),
    farmland_description: new FormControl('', Validators.required),
    soil_type: new FormControl('', Validators.required),
    physical_address: new FormControl('', Validators.required),
    latitude: new FormControl('', Validators.required),
    longitude: new FormControl('', Validators.required)
  });

  initializeFormGroup() {
    this.form.setValue({
     farmland_area : '',
    farmland_experiance: '',
    farmland_description: '',
    soil_type: '',
    physical_address: '',
    latitude: '',
    longitude: ''
    });
  }
}
