import { Component, OnInit } from '@angular/core';
import { AuthService } from '..//../../common/services/auth.service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';


export class SearchModel {
  passport: Number;
}




@Component({
  selector: 'app-farmers-list',
  templateUrl: './farmers-list.component.html',
  styleUrls: ['./farmers-list.component.css']
})
export class FarmersListComponent implements OnInit {
  passportModel: SearchModel = new SearchModel();
  searchForm: FormGroup;
  users: {};
  userLandCount: number;

  constructor(
    private formBuilder: FormBuilder,
    private authService: AuthService
    ) { }

  ngOnInit() {
    this.searchForm = this.formBuilder.group({
      'passport': [this.passportModel.passport, [
        Validators.required,
          ]]});
  }

  // search user by passport
  onSubmitSearch(search) {
  this.authService.getUserByPassport(search.passport)
  .subscribe(data => {
    this.users = data;
    console.log(this.users);
  });
 }
}
