import { NgModule, Component } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

// Common components and services
import { AuthGuard } from './common/guards/auth.guard';
import { RoleGuardGuard } from './common/guards/role-guard.guard';
import { PageNotFoundComponent } from './common/page-not-found/page-not-found.component';

// Gusts section
import { GustComponent } from './gust/gust.component';
import { WelcomeComponent } from './gust/components/welcome/welcome.component';
import { FarmlandsComponent } from './gust/components/farmlands/farmlands.component';
import { CropsComponent } from './gust/components/crops/crops.component';
import { ContactComponent } from './gust/components/contact/contact.component';
import { AboutusComponent } from './gust/components/aboutUs/aboutus.component';
import { RegisterComponent } from './gust/components/register/register.component';

// Farmer section
import { FarmerComponent } from './farmer/farmer.component';
import { ProfileComponent } from './farmer/components/profile/profile.component';
import { HomeComponent } from './farmer/components/home/home.component';
import { MyFarmalndsComponent } from './farmer/components/my-farmalnds/my-farmalnds.component';

// Admin section
import { AdminComponent } from './admin/admin.component';
import { AddCropComponent } from './admin/components/add-crop/add-crop.component';
import { FarmlandRegistrationComponent } from './admin/components/farmland-registration/farmland-registration.component';
import { FarmersListComponent } from './admin/components/farmers-list/farmers-list.component';

// Defining Route mocdule
const appRoutes: Routes =  [
                        {path: '', redirectTo: 'gust', pathMatch : 'full'},
                         {path: 'gust', component: GustComponent,
                         children: [
                          {path: '', redirectTo: 'welcome', pathMatch : 'full'},
                          {path: 'welcome', component: WelcomeComponent},
                          {path: 'farmlands', component: FarmlandsComponent},
                          {path: 'about-us', component: AboutusComponent},
                          {path: 'contact-us', component: ContactComponent},
                          {path: 'crops', component: CropsComponent},
                          { path: 'register', component: RegisterComponent}
                       ]},
                           {path: 'farmers', component : FarmerComponent,
                         children: [
                           {path: '', redirectTo: 'home', pathMatch : 'full'},
                         { path: 'home', component: HomeComponent, canActivate: [AuthGuard] },
                          { path: 'profile', component: ProfileComponent,  canActivate: [AuthGuard] },
                          { path: 'my-farmlands', component: MyFarmalndsComponent, canActivate: [AuthGuard]}
                        ]},
                        {path: 'admin', component : AdminComponent,
                        children: [
                        {path: '', redirectTo: 'farmer-list', pathMatch : 'full'},
                        { path: 'farmer-list', component: FarmersListComponent , canActivate: [AuthGuard, RoleGuardGuard] },
                       { path: 'add-crop', component: AddCropComponent , canActivate: [AuthGuard, RoleGuardGuard] },
                      { path: 'farmland-registration', component: FarmlandRegistrationComponent, canActivate: [AuthGuard, RoleGuardGuard]}
                       ]},
                        {path: '404', component: PageNotFoundComponent},
                        {path: '**', redirectTo: '/404'}
                        ];

@NgModule({
  imports: [RouterModule.forRoot(appRoutes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }

