import { AuthService } from './../../../common/services/auth.service';
import { NotificationServiceService } from '../../../common/services/notification.service';
import { CropService } from '../../../common/services/crop.service';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';


@Component({
  selector: 'app-crop-add',
  templateUrl: './add-crop.component.html',
  styleUrls: ['./add-crop.component.css']
})
export class AddCropComponent implements OnInit {
  constructor(
    private authService: AuthService,
    private router: Router,
    private service: CropService,
    private notify: NotificationServiceService) { }

    ngOnInit() {}

    onSubmit() {
      if (this.service.form.valid) {
        console.log(this.service.form.value);
          this.authService.CropRegister(this.service.form.value).subscribe(data => {
          if (data.success) {
            this.notify.success(`:: ${data.msg}`);
            } else {
              this.notify.warn(`:: ${data.msg}`);
            }
            this.router.navigate(['/admin/add-crop']);
              }); }
       }
}
