import { environment } from './../../../../environments/environment.prod';
import { Component, OnInit } from '@angular/core';
import { MatDialog, MatDialogConfig } from '@angular/material';
// import {Chart } from 'chart.js';
import { AuthService } from '../../../common/services/auth.service';
import { Router } from '@angular/router';
import { HomeComponent } from '../home/home.component';



@Component({
  selector: 'app-my-farmalnds',
  templateUrl: './my-farmalnds.component.html',
  styleUrls: ['./my-farmalnds.component.css']
})
export class MyFarmalndsComponent implements OnInit {
   farmlands: any = [];
   basicInfo: Boolean = true;
   sensor: Boolean = false;
   environment: Boolean = false;

  constructor(
    private authService: AuthService,
    private router: Router,
    private matDialog: MatDialog
  ) { }

  ngOnInit() {
    const user = JSON.parse(localStorage.getItem('user'));
this.authService.getMyFarmland(user._id).subscribe(data => {
    this.farmlands = data;
     console.log(this.farmlands);
  });
  }

  showSesnorData() {
  this.matDialog.open(HomeComponent);
}
 basic() {
this.basicInfo = true;
this.sensor = false;
this.environment = false;
 }

 sensorData() {
  this.basicInfo = false ;
  this.sensor = true;
  this.environment = false;
   }

   weatherEnv() {
    this.basicInfo = false;
    this.sensor = false;
    this.environment = true;
     }

}
