import { Injectable } from '@angular/core';
import { FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { DatePipe } from '@angular/common';

@Injectable({
  providedIn: 'root'
})
export class CropService {

  constructor( private datePipe: DatePipe ) { }

  form: FormGroup = new FormGroup({
    crop_name: new FormControl('', Validators.required),
    crop_description: new FormControl('', Validators.required),
    prefered_soil_type: new FormControl('', Validators.required),
    prefered_season: new FormControl('', Validators.required),
    temprature: new FormControl('', Validators.required),
    moisture: new FormControl('', Validators.required),
    light_intensity: new FormControl('', Validators.required),
    Ph_level: new FormControl('', Validators.required),
    air_temperature: new FormControl('', [Validators.required]),
    humidity: new FormControl('', Validators.required)
  });

  initializeFormGroup() {
    this.form.setValue({
    crop_name: '',
    crop_description: '',
    prefered_soil_type: '',
    prefered_season: '',
    temprature: '',
    moisture: '',
    light_intensity: '',
    Ph_level: '',
    air_temperature: '',
    humidity: ''
    });
  }


}
