import { Component, OnInit } from '@angular/core';
import { AuthService } from '../../../common/services/auth.service';
import { Router } from '@angular/router';
import { MatDialog, MatDialogConfig } from '@angular/material';
import { RegisterComponent } from '../../../gust/components/register/register.component';


@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {
  user: any;
  selectdFile: File = null;

  constructor(
    private authService: AuthService,
    private router: Router,
    private matDialog: MatDialog) { }

  ngOnInit() {
    this.authService.getProfile().subscribe(profile => {
      this.user = profile;
      console.log(this.user);
     });
}

onImageSelected(event) {
  console.log(event);
  this.selectdFile = <File>event.target.files[0];

}
onUpload() {
const fileData =  new FormData();
fileData.append('image', this.selectdFile, this.selectdFile.name);
console.log(this.selectdFile.name);
this.authService.uploadUserProfilePicture(fileData).subscribe(data => {
  if (data.success) {
    console.log('Pofile updated successfully');
     } else {
      console.log('Fail Pofile updatedPofile');
     }
});
}

loadRegisterComponenet() {
  const dialogConfig = new MatDialogConfig();
  dialogConfig.disableClose = true;
  dialogConfig.autoFocus = true;
  dialogConfig.width = '80%';
  dialogConfig.height = '90%';
  this.matDialog.open(RegisterComponent, dialogConfig);
}

}
