import { AuthService } from './../../../common/services/auth.service';
import { NotificationServiceService } from '../../../common/services/notification.service';
import { FarmlandService } from '../../../common/services/farmland.service';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';


@Component({
  selector: 'app-farmland-registration',
  templateUrl: './farmland-registration.component.html',
  styleUrls: ['./farmland-registration.component.css']
})
export class FarmlandRegistrationComponent implements OnInit {
 constructor(
  private authService: AuthService,
  private router: Router,
  private service: FarmlandService,
  private notify: NotificationServiceService) { }

  ngOnInit() {}

  onSubmit() {
    if (this.service.form.valid) {
        this.authService.farmlandRegister(this.service.form.value).subscribe(data => {
        if (data.success) {
          this.notify.success(`:: ${data.msg}`);
          } else {
            this.notify.warn(`:: ${data.msg}`);
          }
          this.router.navigate(['/admin/farmland-registration']);
      });
  }}
}
